import { Component, OnInit } from '@angular/core';
import { AssetNonFormcompoennetComponent } from '../asset-non-formcompoennet/asset-non-formcompoennet.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { NonAssetEditFormComponent } from '../non-asset-edit-form/non-asset-edit-form.component';
import { AssetsServiceService } from 'src/app/core/services/assets-service.service';
import { TitleCasePipe } from '@angular/common';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { RunDepraciationComponentComponent } from '../run-depraciation-component/run-depraciation-component.component';

@Component({
  selector: 'app-asset-non-listcompoennet',
  templateUrl: './asset-non-listcompoennet.component.html',
  styleUrls: ['./asset-non-listcompoennet.component.scss']
})
export class AssetNonListcompoennetComponent implements OnInit {
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public NonAseetData:any=[];
  public aseetId:any;
  constructor(private dialog: MatDialog, private assetsServiceService: AssetsServiceService,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,) { }

  ngOnInit(): void {
    this.assetsServiceService.assetsAdded$.subscribe(() => {
      this.getNonAssetData();
    });
    
    this.getNonAssetData();
  }
  OpenRunDepreciation(){
    const dialogRef: MatDialogRef<RunDepraciationComponentComponent> = this.dialog.open(RunDepraciationComponentComponent, {
      width: '35%',
      height: 'auto',
      data: {},
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  nonAssetsAddForm(){
    const dialogRef: MatDialogRef<AssetNonFormcompoennetComponent> = this.dialog.open(AssetNonFormcompoennetComponent, {
      width: '55%',
      height: '100vh',
      data: {},
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }
  openEditModel(){
    const dialogRef: MatDialogRef<NonAssetEditFormComponent> = this.dialog.open(NonAssetEditFormComponent, {
      width: '55%',
      height: '100vh',
      data: {},
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }

  getNonAssetData(){
 
    this.assetsServiceService.getNONCurrentAssetsData({},this.currentUser.id).then((res:any)=>{
      this.NonAseetData=res.data.totalDecriptionValue;
    }, (err) => {
      if (err.error.expose || err.status==404 ) {
        this.NonAseetData=[];
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  getAssetsId(aseetId){
    this.aseetId=aseetId
  }
  deleteAsset(){
    this.assetsServiceService.deleteAssets({},this.aseetId).then((res:any)=>{
      this.toastService.openSnackBar("Non Current Assets Deleted Successfully!!!");
      this.getNonAssetData();

    }
    , (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  roundDepreciationValue(value: number): number {
    return (value - Math.floor(value) >= 0.5) ? Math.ceil(value) : Math.floor(value);
  }

}
