import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MaincompoennetComponent } from './maincompoennet/maincompoennet.component';
import { AssetListcompoennetComponent } from './asset-listcompoennet/asset-listcompoennet.component';
import { AssetNonListcompoennetComponent } from './asset-non-listcompoennet/asset-non-listcompoennet.component';

const routes: Routes = [
  {
  path: '',
  component : MaincompoennetComponent,
  children : [
    {
      path: '',
      redirectTo: 'aseets-list',
      pathMatch: 'full'
    },
    {
      path: "aseets-list",
      component: AssetListcompoennetComponent
    },
    {
      path: "non-aseets-list",
      component: AssetNonListcompoennetComponent
    },

  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AssetsModuleRoutingModule { }
